<script>
  import ImageGalleryViewer from '@components/ImageGalleryViewer.svelte';
  import { formatTimeLabel } from '$lib/utils/datetime';
  import Reaction from '@components/messenger/Reaction.svelte';
  import { sendMessage } from '$lib/websocket-client';

  let showGallery = false;
  let galleryItems = [];
  let galleryIndex = 0;

  export let msg;
  export let myId;
  export let previousMsg = null;
  export let showStatus = false;

  //$: console.log('msg:', msg);

  // Detect helpers  improved
  const isVideo = (url) => /\.(mp4|webm|mov|ogg)$/i.test(url);

  const isImage = (url) =>
    !isVideo(url) &&
    (/\.(jpg|jpeg|png|gif|webp)$/i.test(url) ||
      url.includes('picsum.photos') ||
      url.includes('giphy.com') ||
      url.startsWith('http'));

  const isFile = (url) => !isImage(url) && !isVideo(url);

  const fromMe = msg.from === myId;
  const sameSender = previousMsg && previousMsg.from === msg.from;

  let showPreview = false; // <= NEW

  export function formatTime(time, locale = 'vi') {
    const d = new Date(time); //  UTC  local auto
    const now = new Date();

    const I18N = {
      vi: {
        today: 'Hm nay',
        yesterday: 'Hm qua'
      },
      en: {
        today: 'Today',
        yesterday: 'Yesterday'
      },
      ja: {
        today: '',
        yesterday: ''
      }
    };

    const texts = I18N[locale] ?? I18N.en;

    const dateFormatter = new Intl.DateTimeFormat(locale, {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    });

    const timeFormatter = new Intl.DateTimeFormat(locale, {
      hour: '2-digit',
      minute: '2-digit'
    });

    const dateStr = dateFormatter.format(d);
    const todayStr = dateFormatter.format(now);

    const yesterday = new Date(now.getFullYear(), now.getMonth(), now.getDate() - 1);
    const yesterdayStr = dateFormatter.format(yesterday);

    const timeStr = timeFormatter.format(d);

    if (dateStr === todayStr) {
      return `${texts.today}, ${timeStr}`;
    }

    if (dateStr === yesterdayStr) {
      return `${texts.yesterday}, ${timeStr}`;
    }

    return `${dateStr}, ${timeStr}`;
  }

  function groupReactions(arr = []) {
    const map = {};
    for (const r of arr) map[r] = (map[r] || 0) + 1;
    return Object.entries(map); // [['',3],['',1]]
  }

  function uiReactions(arr = []) {
    const map = {};
    for (const r of arr) map[r] = (map[r] || 0) + 1;

    return Object.entries(map).sort((a, b) => b[1] - a[1]); // count desc
  }
</script>

<!-- Wrapper -->
<div class="w-full flex {fromMe ? 'justify-end' : 'justify-start'} select-none group">
  <!-- Bubble -->
  <div
    class="relative max-w-[68%] px-3 py-2 pb-4 text-sm shadow-md rounded-2xl transition-all duration-150
    {fromMe
      ? 'bg-gradient-to-br from-blue-200 to-blue-300 text-gray-800 rounded-br-md'
      : 'bg-white text-gray-800 dark:bg-gray-700 dark:text-gray-100 border border-gray-200 dark:border-gray-600 rounded-bl-md'}
    {sameSender ? 'mt-[2px]' : 'mt-2'}">
    {#if !sameSender}
      <p class="text-[10px] opacity-60 mb-[3px]">
        {fromMe ? 'Bn' : msg.fromName || 'User ' + msg.from}
      </p>
    {/if}

    {#if msg.type === 'text'}
      <p class="whitespace-pre-line leading-snug">{msg.message}</p>
    {/if}

    {#if msg.type === 'media'}
      {#if isVideo(msg.url)}
        <video class="rounded-md mt-1 max-h-64" controls src={msg.url}></video>
      {:else if isImage(msg.url)}
        <img
          src={msg.url}
          class="rounded-md mt-1 max-h-64 cursor-pointer hover:opacity-90 transition"
          on:click={() => {
            galleryItems = msg.fullUrl ? [msg.fullUrl] : [msg.url];
            galleryIndex = 0;
            showGallery = true;
          }} />
      {/if}
    {/if}

    {#if msg.type === 'album'}
      <div class="grid grid-cols-2 gap-1 mt-1">
        {#each msg.urls as img, i}
          <img
            src={img}
            class="rounded-md max-h-32 w-full object-cover cursor-pointer hover:opacity-90"
            on:click={() => {
              galleryItems = msg.fullUrls && msg.fullUrls.length ? msg.fullUrls : msg.urls;
              galleryIndex = i;
              showGallery = true;
            }} />
        {/each}
      </div>
    {/if}

    {#if msg.type === 'file' && isFile(msg.url)}
      <a
        href={msg.url}
        target="_blank"
        class="inline-flex items-center gap-2 mt-1 px-3 py-2 rounded-lg border
               bg-white/30 hover:bg-white/50 text-xs
               dark:bg-black/30 dark:hover:bg-black/50">
         {msg.fileName || 'Tp tin nh km'}
      </a>
    {/if}

    <!-- Time -->
    <p class="text-[9px] text-right opacity-60 mt-1">
      {formatTimeLabel(msg.time || msg.created_at)}
    </p>

    {#if msg.reactions?.length}
      <div
        class="absolute -bottom-2
           {fromMe ? 'right-2' : 'left-2'}
           flex items-center gap-[2px]
           px-1 py-[1px]
           bg-white dark:bg-gray-700
           border border-gray-200 dark:border-gray-600
           rounded-full shadow-sm text-[11px] z-10">
        {#each uiReactions(msg.reactions) as [emoji, count]}
          <span class="flex items-center gap-[1px] px-[3px]">
            <span>{emoji}</span>
            <span class="text-[10px] opacity-70">{count}</span>
          </span>
        {/each}
      </div>
    {/if}
  </div>

  <!-- Reaction Button -->
  <div class="opacity-0 group-hover:opacity-100 transition ml-1 self-end">
    <Reaction
      messageId={msg.id}
      {fromMe}
      on:select={(e) => {
        const { messageId, reaction } = e.detail;
        //console.log('Reaction e.detail:', e.detail);

        //  gi ln WS
        sendMessage({
          type: 'reaction',
          messageId,
          reaction
        });
      }} />
  </div>
</div>

<!-- Status -->
{#if showStatus && fromMe}
  <p class="text-[10px] text-right mt-[2px] pr-1 opacity-70 italic">
    {msg.status === 'seen'
      ? ' xem'
      : msg.status === 'sent'
        ? ' gi'
        : msg.status === 'sending'
          ? 'ang gi...'
          : ''}
  </p>
{/if}

<!-- IMAGE POPUP (Lightbox) -->
{#if showPreview}
  <div
    class="fixed inset-0 bg-black/80 z-50 flex items-center justify-center animate-fade"
    on:click={() => (showPreview = false)}>
    <img
      src={msg.url}
      class="max-h-[95%] max-w-[95%] rounded-lg shadow-xl transition scale-100 hover:scale-[1.01]"
      on:click|stopPropagation />
    <button
      class="absolute top-4 right-4 text-white text-2xl font-bold bg-black/40 px-3 py-1 rounded-full hover:bg-black/60"
      on:click={() => (showPreview = false)}>
      
    </button>
  </div>
{/if}

{#if showGallery}
  <ImageGalleryViewer images={galleryItems} index={galleryIndex} onClose={() => (showGallery = false)} />
{/if}

<style>
  .animate-fade {
    animation: fadeIn 0.18s ease-in-out;
  }
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
</style>
