<script>
  // @ts-nocheck
  export let conversation;
  export let selectedId;
  export let onSelect = () => {};

  function formatTime(t) {
    if (!t) return '';
    return new Date(t).toLocaleTimeString([], {
      hour: '2-digit',
      minute: '2-digit'
    });
  }

  //$: console.log('selectedId->conversation', selectedId, conversation);

  $: isSelected = selectedId === conversation.conversationId;
</script>

<!-- svelte-ignore a11y_no_static_element_interactions -->
<!-- svelte-ignore a11y_click_events_have_key_events -->
<!-- svelte-ignore a11y_missing_attribute -->
<div
  on:click={() => onSelect(conversation)}
  class="p-2 rounded-md mb-1 cursor-pointer transition select-none relative
    flex items-center gap-3 border border-transparent
    hover:bg-blue-50 active:scale-[0.98]
    {isSelected ? 'bg-blue-100 border-blue-300 shadow-sm' : ''}
  ">
  <!-- Avatar -->
  <div
    class="w-10 h-10 rounded-full flex items-center justify-center
      bg-gray-200 text-gray-600 font-semibold text-lg uppercase">
    {conversation?.title?.charAt(0) ?? '?'}
  </div>

  <!-- Content -->
  <div class="flex-1 min-w-0">
    <!-- Top row -->
    <div class="flex items-center justify-between">
      <span class="font-medium text-gray-800 truncate">
        {conversation.title}
      </span>

      {#if conversation.lastMessageAt}
        <span class="text-[11px] text-gray-400 whitespace-nowrap">
          {formatTime(conversation.lastMessageAt)}
        </span>
      {/if}
    </div>

    <!-- Message preview -->
    <div class="text-[12px] text-gray-500 truncate mt-0.5">
      {conversation.lastMessagePreview || 'No messages yet'}
    </div>
  </div>

  <!-- Online dot (ch direct) -->
  {#if conversation.type === 'direct'}
    <div
      class="
        w-3 h-3 rounded-full absolute left-2 bottom-2
        {conversation.online ? 'bg-green-500' : 'bg-gray-300'}
      ">
    </div>
  {/if}

  <!-- Group badge -->
  {#if conversation.type === 'group'}
    <span class="absolute right-2 bottom-2 text-[10px] px-1.5 py-0.5 rounded bg-blue-100 text-blue-600">GROUP</span>
  {/if}
</div>
