<script>
  import { tick } from 'svelte';
  import {
    sendMessage,
    loadMessagesWS,
    users,
    messages,
    status,
    myId,
    myName,
    conversations,
    conversationId
  } from '$lib/websocket-client.js';
  import { isDifferentDay } from '$lib/utils/datetime';

  import TimelineDivider from '@components/messenger/TimelineDivider.svelte';
  import Message from '@components/messenger/Message.svelte';
  import Conversion from '@components/messenger/Conversation.svelte';
  import { mgss } from '$lib/messages-sample';

  let input = $state('');
  let selected = $state(null);
  let chatBox;
  let isAtBottom = $state(true);
  let prevLen = $state(0);
  let isLoadingMore = $state(false);

  function selectConversation(conv) {
    console.log('selectConversation:', conv);
    selected = conv.conversationId;

    if (selected) {
      conversationId.set(selected);
    }

    if (selected == 103) {
      messages.set(mgss);
      return;
    }
    messages.set([]);
    loadMessagesWS($conversationId);

    setTimeout(() => {
      if (chatBox) {
        chatBox.scrollTop = chatBox.scrollHeight;
      }
    }, 300);
  }

  function send() {
    console.log('send:', input.trim(), selected);
    if (!input.trim() || !selected) return;

    sendMessage({
      type: 'chat',
      conversationId: selected,
      from: myId,
      message: input.trim()
    });

    input = '';

    setTimeout(() => {
      if (chatBox) {
        chatBox.scrollTop = chatBox.scrollHeight;
      }
    }, 300);
  }

  $effect(() => {
    const len = $messages.length;

    if (chatBox && isAtBottom && len > prevLen) {
      chatBox.scrollTop = chatBox.scrollHeight;
    }

    prevLen = len;
  });

  // function handleScroll() {
  //   if (!chatBox) return;

  //   const threshold = 50;
  //   isAtBottom = chatBox.scrollHeight - chatBox.scrollTop - chatBox.clientHeight < threshold;
  // }

  async function handleScroll() {
    if (!chatBox || isLoadingMore) return;

    // ===== LOAD MORE WHEN SCROLL TOP =====
    if (chatBox.scrollTop < 40) {
      isLoadingMore = true;

      const first = $messages[0];
      if (!first) {
        isLoadingMore = false;
        return;
      }

      const prevHeight = chatBox.scrollHeight;

      loadMessagesWS($conversationId, {
        before: first.created_at
      });

      await tick();

      const newHeight = chatBox.scrollHeight;
      chatBox.scrollTop = newHeight - prevHeight;

      isLoadingMore = false;
    }

    // ===== CHECK BOTTOM =====
    const threshold = 50;
    isAtBottom = chatBox.scrollHeight - chatBox.scrollTop - chatBox.clientHeight < threshold;
  }
</script>

<!-- Dark Mode Wrapper -->
<main
  class="flex h-screen transition-colors duration-300
             bg-gray-100 text-gray-800
            ">
  <!-- SIDEBAR -->
  <aside
    class="w-52 border-r p-3 flex flex-col gap-2 shadow-sm
                bg-white">
    <!-- Header -->
    <p class="text-sm font-medium mb-1 flex items-center gap-1">
      <span class="font-semibold text-blue-600 truncate">{$myName},</span>

      <span
        class="text-[10px] px-2 py-[1px] rounded-full
               {$status.includes('Connected') ? 'bg-green-100 text-green-600' : 'bg-red-100 text-red-600 '}">
        {$status.replace(/\s.*$/, '')}
      </span>
    </p>

    <!-- Conversion List -->
    {#each $conversations as conv}
      <Conversion conversation={conv} onSelect={selectConversation} selectedId={selected} />
    {/each}
  </aside>

  <!-- CHAT AREA -->
  <div class="flex-1 flex flex-col">
    <div
      bind:this={chatBox}
      onscroll={handleScroll}
      class="flex-1 border-b p-3 overflow-y-auto overflow-x-hidden
            border-gray-200">
      {#if selected === null}
        <p class="text-center text-gray-400 mt-5">Select a user to start chatting.</p>
      {:else}
        {#if isLoadingMore}
          <div class="text-center text-xs text-gray-400 my-2">Loading more</div>
        {/if}
        {#each $messages as msg, i (msg.id)}
          {#if i === 0 || isDifferentDay(msg.created_at, $messages[i - 1]?.created_at)}
            {#if msg.created_at}
              <TimelineDivider time={msg.created_at} locale="vi" />
            {/if}
          {/if}
          <Message {msg} myId={$myId} />
        {/each}
      {/if}
    </div>

    <!-- Input -->
    <form
      onsubmit={(e) => {
        e.preventDefault();
        send();
      }}
      class="flex p-3 gap-2 bg-white">
      <input
        bind:value={input}
        placeholder="Type message..."
        class="flex-grow rounded px-3 py-2 border
               border-gray-300
               bg-white
               text-gray-800
               placeholder-gray-400
               focus:outline-none focus:ring-2 focus:ring-blue-400" />

      <button type="submit" class="px-4 py-2 rounded text-white bg-blue-500 hover:bg-blue-600 transition">Send</button>
    </form>
  </div>
</main>
