<script>
  import { createEventDispatcher } from 'svelte';

  export let fromMe = false;
  export let messageId;
  export let reactions = ['', '', '', '', '', ''];

  let open = false;
  let hover = false;
  const dispatch = createEventDispatcher();

  function selectReaction(r) {
    dispatch('select', { messageId, reaction: r });
    open = false;
  }
</script>

<div
  class="relative"
  on:mouseenter={() => (hover = true)}
  on:mouseleave={() => {
    hover = false;
    open = false;
  }}>
  <!-- Button  -->
  <button
    class="text-[12px] bg-gray-200 dark:bg-gray-600 px-1.5 py-0.5 rounded-full hover:scale-110 transition"
    on:click={() => (open = !open)}>
    
  </button>

  <!-- Popup -->
  {#if hover || open}
    <div
      class="absolute bottom-full translate-y-1
         {fromMe ? 'right-0 origin-bottom-right' : 'left-0 origin-bottom-left'}
         flex gap-1 bg-white dark:bg-gray-700
         border border-gray-200 dark:border-gray-600
         rounded-full px-2 py-1 shadow-lg z-50"
      on:mouseenter={() => (open = true)}
      on:mouseleave={() => (open = false)}>
      {#each reactions as r}
        <button class="text-lg hover:scale-125 transition" on:click={() => selectReaction(r)}>
          {r}
        </button>
      {/each}
    </div>
  {/if}
</div>
