const I18N = {
    vi: {
        today: 'Hm nay',
        yesterday: 'Hm qua',
        weekdays: {
            0: 'CN',
            1: 'T2',
            2: 'T3',
            3: 'T4',
            4: 'T5',
            5: 'T6',
            6: 'T7'
        }
    },
    en: {
        today: 'Today',
        yesterday: 'Yesterday'
    }
};

function pad(n) {
    return String(n).padStart(2, '0');
}

/* ========================================================
    DATE LABEL  dng cho TIMELINE
======================================================== */
export function formatDateLabel(time, locale = 'vi') {
    if (!time) return '';

    const d = new Date(time);
    if (isNaN(d.getTime())) return '';

    const now = new Date();
    const i18n = I18N[locale] ?? I18N.vi;

    const sameYear = d.getFullYear() === now.getFullYear();

    const key = (x) =>
        `${x.getFullYear()}-${pad(x.getMonth() + 1)}-${pad(x.getDate())}`;

    const todayKey = key(now);
    const yesterdayKey = key(
        new Date(now.getFullYear(), now.getMonth(), now.getDate() - 1)
    );

    const dateKey = key(d);

    const day = pad(d.getDate());
    const month = pad(d.getMonth() + 1);
    const year = d.getFullYear();

    const weekday =
        locale === 'vi'
            ? i18n.weekdays[d.getDay()]
            : d.toLocaleDateString(locale, { weekday: 'short' });

    if (dateKey === todayKey) {
        return `${i18n.today}, ${day}/${month}`;
    }

    if (dateKey === yesterdayKey) {
        return `${i18n.yesterday}, ${weekday}, ${day}/${month}`;
    }

    if (!sameYear) {
        return `${weekday}, ${day}/${month}/${year}`;
    }

    return `${weekday}, ${day}/${month}`;
}

/* ========================================================
    TIME LABEL  dng trong MESSAGE BUBBLE
======================================================== */
export function formatTimeLabel(time, locale = 'vi') {
    if (!time) return '';
    //console.log('time:', time);
    const d = new Date(time);
    if (isNaN(d.getTime())) return '';

    const now = new Date();
    const i18n = I18N[locale] ?? I18N.vi;

    const key = (x) =>
        `${x.getFullYear()}-${pad(x.getMonth() + 1)}-${pad(x.getDate())}`;

    const todayKey = key(now);
    const yesterdayKey = key(
        new Date(now.getFullYear(), now.getMonth(), now.getDate() - 1)
    );
    const dateKey = key(d);

    const hour = pad(d.getHours());
    const minute = pad(d.getMinutes());
    const timeStr = `${hour}:${minute}`;

    //  HM NAY  ch gi
    if (dateKey === todayKey) {
        return timeStr;
    }

    //  HM QUA  Hm qua + gi
    if (dateKey === yesterdayKey) {
        return `${i18n.yesterday}, ${timeStr}`;
    }

    //  NGY KHC  ch gi
    return timeStr;
}

/* ========================================================
    HELPER  so snh ngy
======================================================== */
export function isDifferentDay(a, b) {
    if (!a || !b) return true;

    const d1 = new Date(a);
    const d2 = new Date(b);

    if (isNaN(d1.getTime()) || isNaN(d2.getTime())) return true;

    return (
        d1.getFullYear() !== d2.getFullYear() ||
        d1.getMonth() !== d2.getMonth() ||
        d1.getDate() !== d2.getDate()
    );
}
